import { faClose } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React, { useRef, useState } from 'react'
import { Toast } from 'primereact/toast';

export default function AddPackage() {

    const toast = useRef(null);

    const inputWeightRef = useRef();

    const inputPriceRef = useRef();

    const [packages, setPackage] = useState([]);

    const [id, setId] = useState(0);

    const [addNewPackage, setAddNewPackage] = useState({
        weight: "",
        price: "",
        id: ""
    });


    const showWarn = (message, summary) => {
        toast.current.show({ severity: summary, summary: summary, detail: message, life: 3000 });
    }

    const handelAddNewPackage = () => {

        if (!inputWeightRef.current.value || !inputPriceRef.current.value) {

            showWarn('Please Fill The Reqriure Inputs', 'warn');

        } else {

            const isDuplicate = packages.some(
                pkg => pkg.weight === addNewPackage.weight && pkg.price === addNewPackage.price
            );

            if (isDuplicate) {

                showWarn(`Please Don't Add Duplicate Package`, 'warn')

            } else {

                // Change new Package Id
                addNewPackage.id = id;

                setPackage(prev => [...prev, addNewPackage]);

                // Change The id Of The Next Package
                setId(prev => prev + 1);

                // Return The Value Of The Inputs as Empty Value
                inputWeightRef.current.value = '';
                inputPriceRef.current.value = '';

                showWarn(`Package Add Successfully`, 'success');

            }
        }
    };


    const removePackage = id => packages?.filter(newPackage => +newPackage.id !== +id);

    return (
        <div className='rounded-[20px] mt-20 border border-[#252525] p-[32px] grid grid-cols-12 gap-10'>


            <div className='col-span-6'>


                <div className='mb-6'>

                    <label htmlFor={'Weight /gm'} className='text-[18px] text-[#252525] font-medium'>Weight /gm</label>

                    <input ref={inputWeightRef} onChange={e => setAddNewPackage(prev => ({ ...prev, weight: e.target.value }))} type='text' id={'Weight /gm'} className='p-3 w-full border-b border-b-[#b3b3b3] placeholder:text-[#b3b3b3]' placeholder={'Enter Weight /gm'} />

                </div>

                <div className='mb-6'>

                    <label htmlFor={'PriceEuro'} className='text-[18px] text-[#252525] font-medium'>Price /Euro</label>

                    <input ref={inputPriceRef} onChange={e => setAddNewPackage(prev => ({ ...prev, price: e.target.value }))} type='number' id={'PriceEuro'} className='p-3 w-full border-b border-b-[#b3b3b3] placeholder:text-[#b3b3b3]' placeholder={'Enter Price /Euro'} />

                </div>

                <button onClick={handelAddNewPackage} className='bg-[#58291E] text-[20px] text-center text-white font-medium w-full py-[16px] px-[24px] rounded-full'>Add Package</button>

            </div>

            <div className='col-span-6'>

                <div className='border border-[#252525] p-[32px] rounded-[20px] h-full max-h-[270px] overflow-y-auto add-package'>

                    {
                        packages.map((item, index) => {
                            return (
                                <div key={index} id={item.id} className='flex items-center justify-between p-3 rounded-[5px] border border-[#252525] mb-5'>

                                    <p className='font-medium text-[#252525]'>{item.price} Euro per {item.weight} gm</p>

                                    <FontAwesomeIcon icon={faClose} onClick={_ => setPackage(removePackage(item.id))} className='text-[#252525] text-[20px] cursor-pointer' />

                                </div>
                            )
                        })
                    }
                </div>

            </div>
            <Toast ref={toast} />
        </div>
    )
}
